function changeImgSize() {
	var imgWidth = $('.gak-article .gak-article-gallery li img').width();
	$('.gak-article-gallery li img').css('height', imgWidth);
}

function changeImgSizeEvent() {
	var imgWidth = $('.gak-event-single .gak-article-gallery li img').width();
	$('.gak-article-gallery li img').css('height', imgWidth);
}

function changeImgSizeModal() {
	var imgWidth1 = $('#news-1 .gak-article-modal .gak-article-gallery li').width();
	var imgWidth2 = $('#news-2 .gak-article-modal .gak-article-gallery li').width();
	var imgWidth3 = $('#news-3 .gak-article-modal .gak-article-gallery li').width();
	$('#news-1 .gak-article-gallery li img').css('height', imgWidth1);
	$('#news-2 .gak-article-gallery li img').css('height', imgWidth2);
	$('#news-3 .gak-article-gallery li img').css('height', imgWidth3);
}


$(document).ready(function () {
	changeImgSize();
	changeImgSizeModal();
	changeImgSizeEvent();
});


window.onresize = function () {
	changeImgSize();
	changeImgSizeModal();
	changeImgSizeEvent();
}

$('[uk-modal]').on('beforeitemshow', function () {
	$('[uk-modal]').each(function () {
		changeImgSizeModal();
	});
});

